
// VARIABLES
$mainblack: #17252A;
$amain: #def2f1;
$grisclaro: #e0e2e2;
$display: #3AAFA9;
$item: #d1c9bf;
$codred: #2b7a78;

// MISC
.wrap{
  width: 100%;
  //height: 100vh;
  position: relative;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center
}
.comp{
  width: 100%;
  height: 194px;
  text-align: center;
  .monitor{
    width: 275px;
    height: 181px;
    display: block;
    margin: 0 auto;
    border-radius: 10px 10px 0px 0px;
    padding: 9px;
    border: solid 1px $grisclaro;
    background-color: black;
  }
  .mid{
    float: left;
    display: block;
    height: 100%;
    position: relative;
    background-color: #DEF2f1;
    width: 50%;
    &.codigo{
      background-color: $mainblack;
    }
  }
  .site{
    overflow: hidden;
    position: absolute;
    width: 105px;
    height: 138px;
    bottom: 0;
    right: 0;
    .topbar{
      width: 100%;

    }
    .cerrar{
      width: 100%;
      padding: 3px;
      line-height: 0;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      border-radius: 4px 4px 0px 0px;
      background-color: rgba(0,0,0,1);
      &>div{
        display: inline-block;
        width: 2px;
        height: 2px;
        border-radius: 50%;
        background: white;
        margin: 0px 1px;
      }
    }
    .inhead{
      padding: 2px;
      height: 5px;
      background-color: #ddd8cf;
      .item{
        width: 10px;
        height: 1px;
        background-color: $item;
        margin: 0 1px;
        display: block;
        float: left;
      }
    }
  }
  .txr{
    text-align: right;
    .item{
      float: right;
    }
  }
  .inslid{
    width: 100%;
    height: 33px;
    background-color: $display;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .item{
      background-color: $item;
      width: 53px;
      height: 2px;
      display: block;
      margin: 0 auto;
      margin-top: 1px;
      &:nth-child(1){
        width: 20px;
      }
      &:nth-child(2){
        margin-top: 3px;
        width: 41px;
      }
      &:nth-child(3){
        width: 32px;
      }
      &:nth-child(4){
        width: 23px;
      }
    }
  }
  .incont{
    padding-top: 10px;
    background: #fefaf0;
    .item{
      background-color: $item;
      width: 53px;
      height: 2px;
      display: block;
      margin: 0 auto;
      margin-top: 1px;
      &:nth-child(1){
        width: 20px;
      }
      &:nth-child(2){
        margin-top: 3px;
        width: 41px;
      }
      &:nth-child(3){
        width: 32px;
      }
      &:nth-child(4){
        width: 23px;
      }
    }
    .wid{
      width: 100%;
      padding: 8px 1px;
      &-bg {
        background-color: $display;
      }
      &-pad {
        padding-top: 4px;
        padding-bottom: 13px;
      }
      .itwid{
        width: 33.333%;
        float: left;
        height: 26px;
        padding: 0px 3px;
        &-right {
          width: 55%;
          float: right;
          height: 26px;
          padding: 0px 3px;
        }
        &>div{
          width: 100%;
          height: 100%;
          background-color: #f5f1e6;
          position: relative;
          .contfoot{
            position: absolute;
            bottom: 0;
            left: 0;
            height: 6px;
            width: 100%;
            background-color: #ebe5dc;
          }
          .conthead{
            position: absolute;
            top: 0;
            left: 0;
            height: 6px;
            width: 100%;
            background-color: #ebe5dc;
          }
        }
      }
    }
    .infoot{
      background-color: $display;
      height: 26px;
      width: 100%;
    }
  }
  @keyframes code {
    0% {
      opacity: 1;
    }
    50% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }
  .codigo{
    padding: 14px 0px 14px 14px;
    .var{
      background-color: $codred;
      animation: code;
      animation-duration: 0.9s;
      animation-iteration-count: infinite;
    }
    .cont{
      background-color: #3aafa9;
      animation: code;
      animation-duration: 1.1s;
      animation-iteration-count: infinite;
    }
    .fun{
      background-color: $amain;
      animation: code;
      animation-duration: 0.9s;
      animation-iteration-count: infinite;
    }
    .atr{
      background-color: #2b7a78;
      animation: code;
      animation-duration: 0.7s;
      animation-iteration-count: infinite;
    }
    .item{
      height: 3px;
      width: 20px;
      float: left;
      margin-right: 3px;
      &.min{
        width: 13px;
      }
      &.lrg{
        width: 30px;
      }
    }
    .line{
      padding: 2px 0;
      width: 100%;
    }
    .tab1{
      padding-left: 5px;
    }
    .tab2{
      padding-left: 8px;
    }
    .tab3{
      padding-left: 10px;
    }
    .tab4{
      padding-left: 12px;
    }
  }
}

.base {
  width: 338px;
  height: 12px;
  background: $grisclaro;
  display: block;
  margin: 0 auto;
  border-radius: 0px 0px 6px 6px;
}
